'use client';

import { useState } from 'react';

export default function ScanPage() {
  const [repoUrl, setRepoUrl] = useState('');
  const [scanType, setScanType] = useState('full');
  const [isScanning, setIsScanning] = useState(false);
  const [scanProgress, setScanProgress] = useState(0);
  const [error, setError] = useState('');

  const validateUrl = (url: string) => {
    const githubUrlPattern = /^https:\/\/github\.com\/[\w-]+\/[\w-]+$/;
    return githubUrlPattern.test(url);
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError('');

    if (!validateUrl(repoUrl)) {
      setError('Please enter a valid GitHub repository URL');
      return;
    }

    setIsScanning(true);
    // Simulate scan progress
    for (let i = 0; i <= 100; i += 10) {
      await new Promise(resolve => setTimeout(resolve, 500));
      setScanProgress(i);
    }
    setIsScanning(false);
  };

  return (
    <div className="max-w-2xl mx-auto space-y-8 p-6">
      <div className="space-y-4">
        <h1 className="text-4xl font-bold">Scan</h1>
      </div>

      <form onSubmit={handleSubmit} className="space-y-6">
        <div className="space-y-2">
          <label htmlFor="repoUrl" className="block text-lg font-medium">
            Repo URL(s)
          </label>
          <input
            id="repoUrl"
            type="text"
            value={repoUrl}
            onChange={(e) => setRepoUrl(e.target.value)}
            placeholder="https://github.com/example/app"
            className="w-full px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-black focus:border-transparent"
          />
          {error && <p className="text-red-500 text-sm">{error}</p>}
        </div>

        <div className="space-y-2">
          <label className="block text-lg font-medium">Scan Depth</label>
          <div className="flex space-x-6">
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="scanType"
                value="full"
                checked={scanType === 'full'}
                onChange={(e) => setScanType(e.target.value)}
                className="form-radio text-black focus:ring-black"
              />
              <span>Full</span>
            </label>
            <label className="flex items-center space-x-2">
              <input
                type="radio"
                name="scanType"
                value="incremental"
                checked={scanType === 'incremental'}
                onChange={(e) => setScanType(e.target.value)}
                className="form-radio text-black focus:ring-black"
              />
              <span>Incremental</span>
            </label>
          </div>
        </div>

        <button
          type="submit"
          disabled={isScanning}
          className="w-full py-2 px-4 bg-black text-white font-semibold rounded-md hover:bg-gray-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-black disabled:opacity-50"
        >
          {isScanning ? 'SCANNING...' : 'BEGIN SCAN'}
        </button>
      </form>

      {isScanning && (
        <div className="space-y-2">
          <label className="block text-lg font-medium">Scan Progress</label>
          <div className="w-full h-4 bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-black transition-all duration-500"
              style={{ width: `${scanProgress}%` }}
            ></div>
          </div>
        </div>
      )}
    </div>
  );
}
